.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  transition: opacity 500ms;

  .pop-up {
    margin: 70px auto;
    padding: 20px;
    background: #fff;
    border-radius: 5px;
    width: 30%;
    position: relative;
    transition: all 5s ease-in-out;

    .close-pop-up {
      position: absolute;
      top: 20px;
      right: 30px;
      transition: all 200ms;
      font-size: 30px;
      font-weight: bold;
      text-decoration: none;
      color: #333;
    }

    .pop-content {
      max-height: 30%;
      overflow: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }
}

.hidden {
  visibility: hidden;
  opacity: 0;
}
